<template>
  <div class="container">
    <headerNavBack :centerTitle="title"></headerNavBack>
    <div class="work_list" @click="__enterWorkRe(item)" v-for="(item, index) in taskList" :key="index">
      <div class="work_item">
        <p>{{item.title}}</p>
        <p>
          <span>期号: </span><span>{{item.expectName}}</span>
          <span class="teach-time">授课时间</span><span>{{item.liveClassId | dateFm}}</span>
        </p>
        <img :src="item.jobStatu == 'UNSUBMITED' ? unSubmit : submit" alt="未提交">
      </div>
    </div>
    <no-data v-show="showNoDataComp" :commonUrl="commonUrl" :commonTitle="commonTitle"></no-data>
  </div>
</template>
<script>
  import headerNavBack from '@/components/headerNavBack'
  import noData from '@/components/noData'
  export default {
    components: {
      headerNavBack,
      noData
    },
    data () {
      return {
        taskList: [],
        title: '',
        unSubmit: require('../../assets/img/nosubmit.png'),
        submit: require('../../assets/img/submit.png'),
        showNoDataComp: 0,
        commonTitle: '这里什么都没有~',
        commonUrl: require('../../assets/img/yuwoxiangguankong.png'),
      }
    },
    methods: {
      back() {
        this.$router.back(-1)
      },
      __enterWorkRe(item){
        this.$router.push({
          name: 'workRe',
          params:{
            taskId: item.taskId,
            title: item.title
          }
        })
      }
    },
    mounted () {
      this.$Indicator.open({
        text: '加载中...',
        spinnerType: 'fading-circle'
      })
      this.title = this.$route.params.title
      this.axios.post('/api/courses/findUserTaskWithCourseChannel',{
        courseId: this.$route.params.courseId
      }).then( data => {
        this.taskList = data.data.result.list
        this.$Indicator.close()
        if(!this.taskList.length){
          this.showNoDataComp  = 1
        }
      }).catch( err => {
        console.log(err)
      })
    }
  }
</script>


<style lang="less" scoped>
  .container {
    background-color: #eee;
  }
  .header {
    display: flex;
    height: 2.5rem;
    background-color: #fff;
    div {
      flex: 2;
      padding-left: 1.25rem;
      height: 2.5rem;
      vertical-align: middle;
      img {
        width: 1.25rem;
        height: 1.875rem;
        vertical-align: middle;
      }
    }
    span {
      flex: 8;
      text-indent: 3em;
      vertical-align: middle;
      line-height: 1.875rem;
    }
  } // 头部
  .work_list{
    padding: .625rem;
    .work_item{
      position: relative;
      height: 6.25rem;
      background-color: #fff;
      p{
        width: 100%;
        height: 2.8125rem;
        line-height: 2.8125rem;
        text-indent: 1em;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
        &:nth-child(1){
          border-bottom: 1px solid #eee;  
        }
        &>span{
          font-size: .875rem;
        }
      }
      img{
        position: absolute;
        top: 0;
        right: 0;
        height: 60%;
      }
    }
  }
  .teach-time{
    margin: 0 .625rem;
  }
</style>



