<template>
  <div>
    <img :src="commonUrl" :alt="commonTitle"><br>
    <span>{{commonTitle}}</span>
  </div>
</template>
<script>
  export default {
    props: [
      'commonUrl',
      'commonTitle'
    ],
  }
</script>
<style lang="less" scoped>
div{
  text-align: center;
}
</style>



